;\n          font-weight: bold;\n          display: flex;\n          align-items: center;\n          gap: 8px;\n        \"\n      >\n         \n      </h3>\n      <span\n        v-if=\"table_history.length > 0\"\n        class=\"count-badge\"\n        style=\"background: #4a9eff; color: white; padding: 4px 8px; border-radius: 12px; font-size: 12px\"\n        >{{ table_history.length }} </span\n      >\n    </div>\n\n    <!--  -->\n    <div\n      v-if=\"table_history && table_history.length > 0\"\n      class=\"history-list\"\n      style=\"display: flex; flex-direction: column; gap: 15px; margin-top: 20px\"\n    >\n      <div\n        v-for=\"(item, index) in table_history\"\n        :key=\"index\"\n        class=\"history-item\"\n        style=\"\n          background: #2a2a2a;\n          border: 1px solid #444;\n          border-radius: 8px;\n          padding: 15px;\n          transition: all 0.3s ease;\n        \"\n      >\n        <div\n          class=\"history-header\"\n          style=\"\n            padding: 12px 15px;\n            background: #1a1a1a;\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n            cursor: pointer;\n            user-select: none;\n            transition: background 0.2s;\n            border-radius: 6px;\n          \"\n          @click=\"toggleTableExpanded(index)\"\n        >\n          <div class=\"history-info\" style=\"display: flex; align-items: center; gap: 10px; flex: 1\">\n            <span class=\"history-number\" style=\"color: #4a9eff; font-weight: bold\">#{{ index + 1 }}</span>\n            <span class=\"history-range\" style=\"color: #888; font-size: 12px\"\n              > {{ item.start_id }} - {{ item.end_id }}</span\n            >\n          </div>\n          <span class=\"expand-icon\" style=\"color: #888; font-size: 12px\">{{\n            isTableExpanded(index) ? '' : ''\n          }}</span>\n        </div>\n        <div\n          v-if=\"isTableExpanded(index)\"\n          class=\"history-content\"\n          style=\"padding: 15px; background: #2a2a2a; border-radius: 0 0 6px 6px\"\n        >\n          <div\n            class=\"history-actions\"\n            style=\"\n              display: flex;\n              gap: 10px;\n              margin-bottom: 12px;\n              padding-bottom: 8px;\n              border-bottom: 1px solid #3a3a3a;\n              flex-wrap: wrap;\n            \"\n          >\n            <button\n              class=\"copy-button\"\n              style=\"\n                padding: 8px 16px;\n                background: linear-gradient(135deg, #28a745 0%, #20c997 100%);\n                border: none;\n                border-radius: 6px;\n                color: white;\n                cursor: pointer;\n                font-size: 12px;\n                font-weight: 600;\n                transition: all 0.3s ease;\n                display: flex;\n                align-items: center;\n                gap: 6px;\n                box-shadow: 0 3px 12px rgba(40, 167, 69, 0.3);\n                position: relative;\n                overflow: hidden;\n              \"\n              @click=\"copyTable(item)\"\n            >\n              <div\n                style=\"\n                  position: absolute;\n                  top: 0;\n                  left: -100%;\n                  width: 100%;\n                  height: 100%;\n                  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);\n                  transition: left 0.5s;\n                \"\n                class=\"shimmer-effect\"\n              ></div>\n              <i class=\"fa-solid fa-copy\" style=\"font-size: 12px\"></i> \n            </button>\n            <button\n              class=\"delete-button\"\n              style=\"\n                padding: 8px 16px;\n                background: linear-gradient(135deg, #ff6b6b 0%, #ee5a52 100%);\n                border: none;\n                border-radius: 6px;\n                color: white;\n                cursor: pointer;\n                font-size: 12px;\n                font-weight: 600;\n                transition: all 0.3s ease;\n                display: flex;\n                align-items: center;\n                gap: 6px;\n                box-shadow: 0 3px 12px rgba(255, 107, 107, 0.3);\n                position: relative;\n                overflow: hidden;\n              \"\n              @click=\"deleteTable(index)\"\n            >\n              <div\n                style=\"\n                  position: absolute;\n                  top: 0;\n                  left: -100%;\n                  width: 100%;\n                  height: 100%;\n                  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);\n                  transition: left 0.5s;\n                \"\n                class=\"shimmer-effect\"\n              ></div>\n              <i class=\"fa-solid fa-trash\" style=\"font-size: 12px\"></i> \n            </button>\n          </div>\n          <div class=\"table-display\" style=\"overflow-x: auto; border-radius: 6px; border: 1px solid #3a3a3a\">\n            <table style=\"width: 100%; border-collapse: collapse; background: #1a1a1a\">\n              <thead style=\"background: #2a2a2a\">\n                <tr>\n                  <th\n                    v-for=\"(header, idx) in item.headers\"\n                    :key=\"idx\"\n                    style=\"\n                      padding: 12px 16px;\n                      text-align: left;\n                      font-weight: bold;\n                      color: #fff;\n                      border-bottom: 2px solid #3a3a3a;\n                      border-right: 1px solid #3a3a3a;\n                    \"\n                  >\n                    {{ header }}\n                  </th>\n                </tr>\n              </thead>\n              <tbody>\n                <tr v-for=\"(row, rowIdx) in item.data\" :key=\"rowIdx\" style=\"border-bottom: 1px solid #3a3a3a\">\n                  <td\n                    v-for=\"(cell, cellIdx) in row\"\n                    :key=\"cellIdx\"\n                    style=\"padding: 10px 16px; color: #e0e0e0; border-right: 1px solid #3a3a3a\"\n                  >\n                    {{ cell }}\n                  </td>\n                </tr>\n              </tbody>\n            </table>\n          </div>\n        </div>\n      </div>\n    </div>\n    <div v-else class=\"empty-state\" style=\"text-align: center; padding: 40px 20px; color: #888\">\n      <i class=\"fa-solid fa-table\" style=\"font-size: 48px; margin-bottom: 10px; opacity: 0.3\"></i>\n      <p style=\"margin: 10px 0; font-size: 16px\"></p>\n      <small style=\"font-size: 12px; color: #666\">\"\"</small>\n    </div>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref } from 'vue';\nimport { getChatIdSafe } from '../utils';\n\n// \nconst table_history = ref<Array<{ start_id: number; end_id: number; headers: string[]; data: string[][] }>>([]);\nconst tableExpandedState = ref<Map<number, boolean>>(new Map());\n\n// \nconst refreshTableHistory = () => {\n  try {\n    const chat_id = getChatIdSafe();\n    if (!chat_id) {\n      console.warn('ID');\n      table_history.value = [];\n      return;\n    }\n\n    const chat_vars = getVariables({ type: 'chat' });\n    console.log(':', chat_vars);\n    console.log(':', chat_vars.table_history);\n\n    table_history.value = chat_vars.table_history || [];\n    console.log(':', table_history.value.length);\n  } catch (e) {\n    console.error(':', e);\n    table_history.value = [];\n  }\n};\n\n// \nconst toggleTableExpanded = (index: number) => {\n  const current = tableExpandedState.value.get(index) || false;\n  tableExpandedState.value.set(index, !current);\n};\n\nconst isTableExpanded = (index: number) => {\n  return tableExpandedState.value.get(index) || false;\n};\n\n//  - iframe\nconst copyToClipboard = async (text: string, successMessage: string = '') => {\n  console.log(':', text.length);\n\n  try {\n    // iframe\n    if (window.parent && window.parent !== window) {\n      console.log('iframe');\n      const parentSuccess = await tryParentWindowCopy(text);\n      if (parentSuccess) {\n        console.log('');\n        window.toastr.success(successMessage);\n        return;\n      }\n    }\n\n    // 1:  Clipboard API\n    if (navigator.clipboard && window.isSecureContext) {\n      console.log(' Clipboard API');\n      try {\n        await navigator.clipboard.writeText(text);\n        console.log('Clipboard API ');\n        window.toastr.success(successMessage);\n        return;\n      } catch (clipboardErr) {\n        console.warn('Clipboard API :', clipboardErr);\n        // \n      }\n    }\n\n    // 2:  execCommand \n    console.log(' execCommand ');\n    const success = await execCommandCopy(text);\n    if (success) {\n      console.log('execCommand ');\n      window.toastr.success(successMessage);\n      return;\n    }\n\n    // 3: \n    console.log('');\n    showManualCopyDialog(text);\n  } catch (err) {\n    console.error(':', err);\n    // \n    showManualCopyDialog(text);\n  }\n};\n\n// \nconst tryParentWindowCopy = async (text: string): Promise<boolean> => {\n  return new Promise(resolve => {\n    try {\n      const parentDoc = window.parent.document;\n      const parentBody = window.parent.document.body;\n\n      //  textarea \n      const textarea = parentDoc.createElement('textarea');\n      textarea.value = text;\n      textarea.style.cssText = `\n        position: fixed;\n        left: -9999px;\n        top: -9999px;\n        opacity: 0;\n        pointer-events: none;\n        z-index: -1000;\n      `;\n      textarea.setAttribute('readonly', '');\n\n      parentBody.appendChild(textarea);\n\n      // \n      textarea.focus();\n      textarea.select();\n      textarea.setSelectionRange(0, text.length);\n\n      // \n      const success = parentDoc.execCommand('copy');\n\n      // \n      parentBody.removeChild(textarea);\n\n      resolve(success);\n    } catch (err) {\n      console.error(':', err);\n      resolve(false);\n    }\n  });\n};\n\n// execCommand \nconst execCommandCopy = async (text: string): Promise<boolean> => {\n  return new Promise(resolve => {\n    try {\n      //  textarea \n      const textarea = document.createElement('textarea');\n      textarea.value = text;\n      textarea.style.position = 'fixed';\n      textarea.style.left = '-9999px';\n      textarea.style.top = '-9999px';\n      textarea.style.opacity = '0';\n      textarea.style.pointerEvents = 'none';\n      textarea.setAttribute('readonly', '');\n\n      document.body.appendChild(textarea);\n\n      // \n      textarea.select();\n      textarea.setSelectionRange(0, text.length);\n\n      // \n      const success = document.execCommand('copy');\n\n      // \n      document.body.removeChild(textarea);\n\n      resolve(success);\n    } catch (err) {\n      console.error('execCommand :', err);\n      resolve(false);\n    }\n  });\n};\n\n//  - \nconst showManualCopyDialog = (text: string) => {\n  console.log(':', text.length);\n\n  try {\n    // document\n    const parentDoc = window.parent.document;\n    const parentBody = window.parent.document.body;\n\n    // \n    const existingOverlay = parentDoc.getElementById('manual-copy-overlay');\n    if (existingOverlay) {\n      parentBody.removeChild(existingOverlay);\n    }\n\n    // \n    const overlay = parentDoc.createElement('div');\n    overlay.id = 'manual-copy-overlay';\n    overlay.style.cssText = `\n      position: fixed;\n      top: 0;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      background: rgba(0, 0, 0, 0.95);\n      z-index: 999999;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;\n    `;\n\n    // \n    const container = parentDoc.createElement('div');\n    container.style.cssText = `\n      background: #2a2a2a;\n      border: 2px solid #4a9eff;\n      border-radius: 12px;\n      padding: 25px;\n      max-width: 900px;\n      width: 95%;\n      max-height: 85vh;\n      display: flex;\n      flex-direction: column;\n      box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);\n    `;\n\n    // \n    const header = parentDoc.createElement('div');\n    header.innerHTML = `\n      <h3 style=\"margin: 0 0 15px 0; color: #4a9eff; text-align: center; font-size: 18px; font-weight: 600;\">\n         \n      </h3>\n      <div style=\"background: #1a1a1a; border: 1px solid #444; border-radius: 6px; padding: 12px; margin-bottom: 15px;\">\n        <p style=\"margin: 0 0 8px 0; color: #e0e0e0; font-size: 14px;\">\n          <strong></strong>\n        </p>\n        <ol style=\"margin: 0; padding-left: 20px; color: #ccc; font-size: 13px; line-height: 1.6;\">\n          <li></li>\n          <li> <kbd style=\"background: #444; padding: 2px 6px; border-radius: 3px; color: #fff;\">Ctrl+A</kbd> </li>\n          <li> <kbd style=\"background: #444; padding: 2px 6px; border-radius: 3px; color: #fff;\">Ctrl+C</kbd> </li>\n          <li>\"\" <kbd style=\"background: #444; padding: 2px 6px; border-radius: 3px; color: #fff;\">Esc</kbd> </li>\n        </ol>\n      </div>\n    `;\n\n    // \n    const textarea = parentDoc.createElement('textarea');\n    textarea.value = text;\n    textarea.style.cssText = `\n      width: 100%;\n      height: 400px;\n      background: #1a1a1a;\n      border: 2px solid #444;\n      border-radius: 6px;\n      padding: 15px;\n      color: #e0e0e0;\n      font-family: 'Courier New', 'Consolas', monospace;\n      font-size: 13px;\n      line-height: 1.5;\n      resize: vertical;\n      white-space: pre-wrap;\n      word-wrap: break-word;\n      outline: none;\n      margin-bottom: 20px;\n      box-sizing: border-box;\n    `;\n\n    // \n    const buttonContainer = parentDoc.createElement('div');\n    buttonContainer.s