                                console.log(` ${messageId}`);
                            }
                            catch (error) {
                                console.error(` ${messageId} :`, error);
                            }
                        }
                        window.toastr.success('');
                    }
                    catch (error) {
                        console.error('API:', error);
                        window.toastr.error('API: ' + error.message);
                    }
                }
