                    // 
                    saveHiddenMessages();
                    window.toastr.success(` ${hiddenCount} `);
                    hide_range.value = ''; // 
                }
                else {
                    window.toastr.warning('');
                }
            }
            catch (error) {
                console.error(':', error);
                window.toastr.error(': ' + error.message);
            }
