      <h3\n        style=\"\n          margin: 0;\n          color: #fff;\n          font-size: 15px !important;\n          font-weight: bold;\n          display: flex;\n          align-items: center;\n          gap: 8px;\n        \"\n      >\n         \n      </h3>\n      <div class=\"header-actions\" style=\"display: flex; align-items: center; gap: 10px\">\n        <span\n          v-if=\"summary_history.length > 0\"\n          class=\"count-badge\"\n          style=\"background: #4a9eff; color: white; padding: 4px 8px; border-radius: 12px; font-size: 12px\"\n          >{{ summary_history.length }} </span\n        >\n        <button\n          class=\"mini-button refresh-button\"\n          style=\"\n            padding: 6px 12px;\n            background: #2a2a2a;\n            border: 1px solid #3a3a3a;\n            border-radius: 4px;\n            color: #e0e0e0;\n            cursor: pointer;\n            font-size: 12px;\n            transition: all 0.2s;\n            display: flex;\n            align-items: center;\n            gap: 6px;\n          \"\n          @click=\"handleRefreshData\"\n        >\n          <i class=\"fa-solid fa-refresh\"></i> \n        </button>\n        <button\n          class=\"mini-button create-worldbook-button\"\n          style=\"\n            padding: 6px 12px;\n            background: #51cf66;\n            border: 1px solid #40c057;\n            border-radius: 4px;\n            color: white;\n            cursor: pointer;\n            font-size: 12px;\n            transition: all 0.2s;\n            display: flex;\n            align-items: center;\n            gap: 6px;\n          \"\n          @click=\"createSummaryWorldbook\"\n        >\n          <i class=\"fa-solid fa-plus\"></i> \n        </button>\n        <button\n          class=\"mini-button debug-button\"\n          style=\"\n            padding: 6px 12px;\n            background: #ffc107;\n            border: 1px solid #ffb300;\n            border-radius: 4px;\n            color: #000;\n            cursor: pointer;\n            font-size: 12px;\n            transition: all 0.2s;\n            display: flex;\n            align-items: center;\n            gap: 6px;\n          \"\n          @click=\"showDebugPanel\"\n        >\n          <i class=\"fa-solid fa-bug\"></i> \n        </button>\n      </div>\n    </div>\n\n    <div\n      v-if=\"summary_history && summary_history.length > 0\"\n      class=\"history-list\"\n      style=\"display: flex; flex-direction: column; gap: 15px; margin-top: 20px\"\n    >\n      <div\n        v-for=\"(item, index) in summary_history\"\n        :key=\"index\"\n        class=\"history-item\"\n        style=\"\n          background: #2a2a2a;\n          border: 1px solid #444;\n          border-radius: 8px;\n          padding: 15px;\n          transition: all 0.3s ease;\n        \"\n      >\n        <div\n          class=\"history-header\"\n          style=\"\n            padding: 12px 15px;\n            background: #1a1a1a;\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n            cursor: pointer;\n            user-select: none;\n            transition: background 0.2s;\n            border-radius: 6px;\n          \"\n          @click=\"toggleExpanded(index)\"\n        >\n          <div class=\"history-info\" style=\"display: flex; align-items: center; gap: 10px; flex: 1\">\n            <span class=\"history-number\" style=\"color: #4a9eff; font-weight: bold\">#{{ index + 1 }}</span>\n            <span class=\"history-range\" style=\"color: #888; font-size: 12px\"\n              > {{ item.start_id }} - {{ item.end_id }}</span\n            >\n          </div>\n          <span class=\"expand-icon\" style=\"color: #888; font-size: 12px\">{{ isExpanded(index) ? '' : '' }}</span>\n        </div>\n        <div\n          v-if=\"isExpanded(index)\"\n          class=\"history-content\"\n          style=\"padding: 15px; background: #2a2a2a; border-radius: 0 0 6px 6px\"\n        >\n          <div\n            class=\"history-actions\"\n            style=\"display: flex; gap: 8px; margin-bottom: 12px; padding-bottom: 8px; border-bottom: 1px solid #3a3a3a\"\n          >\n            <button\n              class=\"mini-button\"\n              style=\"\n                flex: 1;\n                padding: 6px 12px;\n                background: #2a2a2a;\n                border: 1px solid #3a3a3a;\n                border-radius: 4px;\n                color: #e0e0e0;\n                cursor: pointer;\n                font-size: 12px;\n                transition: background 0.2s;\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                gap: 6px;\n              \"\n              @click=\"copySummary(item.content)\"\n            >\n              <i class=\"fa-solid fa-copy\"></i> \n            </button>\n            <button\n              class=\"mini-button worldbook-button\"\n              style=\"\n                flex: 1;\n                padding: 6px 12px;\n                background: #4a9eff;\n                border: 1px solid #5aaeff;\n                border-radius: 4px;\n                color: white;\n                cursor: pointer;\n                font-size: 12px;\n                transition: all 0.2s;\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                gap: 6px;\n              \"\n              @click=\"bindToWorldbook(item.content, index)\"\n            >\n              <i class=\"fa-solid fa-book\"></i> \n            </button>\n            <button\n              class=\"mini-button delete-button\"\n              style=\"\n                flex: 1;\n                padding: 6px 12px;\n                background: #ff6b6b;\n                border: 1px solid #ff5252;\n                border-radius: 4px;\n                color: white;\n                cursor: pointer;\n                font-size: 12px;\n                transition: all 0.2s;\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                gap: 6px;\n              \"\n              @click=\"deleteSummary(index)\"\n            >\n              <i class=\"fa-solid fa-trash\"></i> \n            </button>\n          </div>\n          <div\n            class=\"history-text\"\n            style=\"\n              font-family: 'Courier New', monospace;\n              font-size: 13px;\n              line-height: 1.6;\n              color: #e0e0e0;\n              white-space: pre-wrap;\n              word-wrap: break-word;\n              user-select: text;\n              cursor: text;\n            \"\n          >\n            {{ item.content }}\n          </div>\n        </div>\n      </div>\n    </div>\n    <div v-else class=\"empty-state\" style=\"text-align: center; padding: 40px 20px; color: #888\">\n      <i class=\"fa-solid fa-inbox\" style=\"font-size: 48px; margin-bottom: 10px; opacity: 0.3\"></i>\n      <p style=\"margin: 10px 0; font-size: 16px\"></p>\n      <small style=\"font-size: 12px; color: #666\">\"\"</small>\n    </div>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref } from 'vue';\nimport { useSummaryHistoryStore } from '../settings';\nimport { getChatIdSafe } from '../utils';\n\nconst historyStore = useSummaryHistoryStore();\n\n// \nconst summary_history = ref<Array<{ start_id: number; end_id: number; content: string }>>([]);\nconst expandedState = ref<Map<number, boolean>>(new Map());\n\n// \nconst refreshSummaryHistory = () => {\n  try {\n    summary_history.value = historyStore.getSummaryHistory();\n    console.log(':', summary_history.value.length);\n  } catch (e) {\n    console.error(':', e);\n    summary_history.value = [];\n  }\n};\n\n// \nconst toggleExpanded = (index: number) => {\n  const current = expandedState.value.get(index) || false;\n  expandedState.value.set(index, !current);\n};\n\nconst isExpanded = (index: number) => {\n  return expandedState.value.get(index) || false;\n};\n\n// \nconst handleRefreshData = async () => {\n  console.log('');\n  try {\n    await refreshSummaryHistory();\n    window.toastr.success('');\n  } catch (error) {\n    console.error(':', error);\n    window.toastr.error(': ' + (error as Error).message);\n  }\n};\n\n//  - iframe\nconst copyToClipboard = async (text: string, successMessage: string = '') => {\n  console.log(':', text.length);\n\n  try {\n    // iframe\n    if (window.parent && window.parent !== window) {\n      console.log('iframe');\n      const parentSuccess = await tryParentWindowCopy(text);\n      if (parentSuccess) {\n        console.log('');\n        window.toastr.success(successMessage);\n        return;\n      }\n    }\n\n    // 1:  Clipboard API\n    if (navigator.clipboard && window.isSecureContext) {\n      console.log(' Clipboard API');\n      try {\n        await navigator.clipboard.writeText(text);\n        console.log('Clipboard API ');\n        window.toastr.success(successMessage);\n        return;\n      } catch (clipboardErr) {\n        console.warn('Clipboard API :', clipboardErr);\n        // \n      }\n    }\n\n    // 2:  execCommand \n    console.log(' execCommand ');\n    const success = await execCommandCopy(text);\n    if (success) {\n      console.log('execCommand ');\n      window.toastr.success(successMessage);\n      return;\n    }\n\n    // 3: \n    console.log('');\n    showManualCopyDialog(text);\n  } catch (err) {\n    console.error(':', err);\n    // \n    showManualCopyDialog(text);\n  }\n};\n\n// \nconst tryParentWindowCopy = async (text: string): Promise<boolean> => {\n  return new Promise(resolve => {\n    try {\n      const parentDoc = window.parent.document;\n      const parentBody = window.parent.document.body;\n\n      //  textarea \n      const textarea = parentDoc.createElement('textarea');\n      textarea.value = text;\n      textarea.style.cssText = `\n        position: fixed;\n        left: -9999px;\n        top: -9999px;\n        opacity: 0;\n        pointer-events: none;\n        z-index: -1000;\n      `;\n      textarea.setAttribute('readonly', '');\n\n      parentBody.appendChild(textarea);\n\n      // \n      textarea.focus();\n      textarea.select();\n      textarea.setSelectionRange(0, text.length);\n\n      // \n      const success = parentDoc.execCommand('copy');\n\n      // \n      parentBody.removeChild(textarea);\n\n      resolve(success);\n    } catch (err) {\n      console.error(':', err);\n      resolve(false);\n    }\n  });\n};\n\n// execCommand \nconst execCommandCopy = async (text: string): Promise<boolean> => {\n  return new Promise(resolve => {\n    try {\n      //  textarea \n      const textarea = document.createElement('textarea');\n      textarea.value = text;\n      textarea.style.position = 'fixed';\n      textarea.style.left = '-9999px';\n      textarea.style.top = '-9999px';\n      textarea.style.opacity = '0';\n      textarea.style.pointerEvents = 'none';\n      textarea.setAttribute('readonly', '');\n\n      document.body.appendChild(textarea);\n\n      // \n      textarea.select();\n      textarea.setSelectionRange(0, text.length);\n\n      // \n      const success = document.execCommand('copy');\n\n      // \n      document.body.removeChild(textarea);\n\n      resolve(success);\n    } catch (err) {\n      console.error('execCommand :', err);\n      resolve(false);\n    }\n  });\n};\n\n//  - \nconst showManualCopyDialog = (text: string) => {\n  console.log(':', text.length);\n\n  try {\n    // document\n    const parentDoc = window.parent.document;\n    const parentBody = window.parent.document.body;\n\n    // \n    const existingOverlay = parentDoc.getElementById('manual-copy-overlay');\n    if (existingOverlay) {\n      parentBody.removeChild(existingOverlay);\n    }\n\n    // \n    const overlay = parentDoc.createElement('div');\n    overlay.id = 'manual-copy-overlay';\n    overlay.style.cssText = `\n      position: fixed;\n      top: 0;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      background: rgba(0, 0, 0, 0.95);\n      z-index: 999999;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;\n    `;\n\n    // \n    const container = parentDoc.createElement('div');\n    container.style.cssText = `\n      background: #2a2a2a;\n      border: 2px solid #4a9eff;\n      border-radius: 12px;\n      padding: 25px;\n      max-width: 900px;\n      width: 95%;\n      max-height: 85vh;\n      display: flex;\n      flex-direction: column;\n      box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);\n    `;\n\n    // \n    const header = parentDoc.createElement('div');\n    header.innerHTML = `\n      <h3 style=\"margin: 0 0 15px 0; color: #4a9eff; text-align: center; font-size: 18px; font-weight: 600;\">\n         \n      </h3>\n      <div style=\"background: #1a1a1a; border: 1px solid #444; border-radius: 6px; padding: 12px; margin-bottom: 15px;\">\n        <p style=\"margin: 0 0 8px 0; color: #e0e0e0; font-size: 14px;\">\n          <strong></strong>\n        </p>\n        <ol style=\"margin: 0; padding-left: 20px; color: #ccc; font-size: 13px; line-height: 1.6;\">\n          <li></li>\n          <li> <kbd style=\"background: #444; padding: 2px 6px; border-radius: 3px; color: #fff;\">Ctrl+A</kbd> </li>\n          <li> <kbd style=\"background: #444; padding: 2px 6px; border-radius: 3px; color: #fff;\">Ctrl+C</kbd> </li>\n          <li>\"\" <kbd style=\"background: #444; padding: 2px 6px; border-radius: 3px; color: #fff;\">Esc</kbd> </li>\n        </ol>\n      </div>\n    `;\n\n    // \n    const textarea = parentDoc.createElement('textarea');\n    textarea.value = text;\n    textarea.style.cssText = `\n      width: 100%;\n      height: 400px;\n      background: #1a1a1a;\n      border: 2px solid #444;\n      border-radius: 6px;\n      padding: 15px;\n      color: #e0e0e0;\n      font-family: 'Courier New', 'Consolas', monospace;\n      font-size: 13px;\n      line-height: 1.5;\n      resize: vertical;\n      white-space: pre-wrap;\n      word-wrap: break-word;\n      outline: none;\n      margin-bottom: 20px;\n      box-sizing: border-box;\n    `;\n\n    // \n    const buttonContainer = parentDoc.createElement('div');\n    buttonContainer.style.cssText = `\n      display: flex;\n      gap: 12px;\n      justify-content: center;\n      align-items: center;\n    `;\n\n    // \n    const closeButton = parentDoc.createElement('button');\n    closeButton.textContent = '';\n    closeButton.style.cssText = `\n      background: #4a9eff;\n      color: white;\n      border: none;\n      padding: 12px 24px;\n      border-radius: 6px;\n      cursor: pointer;\n      font-size: 14px;\n      font-weight: 500;\n      transition: background 0.2s;\n    `;\n    closeButton.onmouseover = () => {\n      closeButton.style.background = '#5ba8ff';\n    };\n    closeButton.onmouseout = () => {\n      closeButton.style.background = '#4a9eff';\n    };\n\n    // \n    const copyButton = parentDoc.createElement('button');\n    copyButton.textContent = '';\n    copyButton.style.cssText = `\n      background: #28a745;\n      color: white;\n      border: none;\n      padding: 12px 24px;\n      border-radius: 6px;\n      cursor: pointer;\n      font-size: 14px;\n      font-weight: 500;\n      transition: background 0.2s;\n    `;\n    copyButton.onmouseover = () => {\n      copyButton.style.background = '#218838';\n    };\n    copyButton.onmouseout = () => {\n      copyButton.style.background = '#28a745';\n    };\n\n    // \n    buttonContainer.appendChild(copyButton);\n    buttonContainer.appendChild(closeButton);\n\n    container.appendChild(header);\n    container.appendChild(textarea);\n    container.appendChild(buttonContainer);\n    overlay.appendChild(container);\n\n    // \n    parentBody.appendChild(overlay);\n\n    // \n    closeButton.onclick = () => {\n      console.log('');\n      parentBody.removeChild(overlay);\n    };\n\n    copyButton.onclick = async () => {\n      console.log('');\n      try {\n        // \n        textarea.focus();\n        textarea.select();\n\n        // \n        const success = document.execCommand('copy');\n        if (success) {\n          window.toastr.success('');\n          parentBody.removeChild(overlay);\n        } else {\n          window.toastr.warning('');\n        }\n      } catch (err) {\n        console.error(':', err);\n        window.toastr.warning('');\n      }\n    };\n\n    // \n    overlay.onclick = e => {\n      if (e.target === overlay) {\n        console.log('');\n        parentBody.removeChild(overlay);\n      }\n    };\n\n    // \n    const handleKeyDown = (e: KeyboardEvent) => {\n      if (e.key === 'Escape') {\n        console.log('ESC');\n        parentBody.removeChild(overlay);\n        parentDoc.removeEventListener('keydown', handleKeyDown);\n      }\n    };\n    parentDoc.addEventListener('keydown', handleKeyDown);\n\n    // \n    setTimeout(() => {\n      console.log('');\n      textarea.focus();\n      textarea.select();\n    }, 200);\n\n    // \n    window.toastr.info('');\n  } catch (err) {\n    console.error(':', err);\n    window.toastr.error('');\n  }\n};\n\n//  - \nconst copySummary = (content: string) => {\n  copyToClipboard(content, '');\n};\n\n// \nconst deleteSummary = (index: number) => {\n  if (confirm('')) {\n    try {\n      const chat_id = getChatIdSafe();\n      if (!chat_id) {\n        window.toastr.error('');\n        return;\n      }\n\n      summary_history.value.splice(index, 1);\n      insertOrAssignVariables({ summary_history: summary_history.value }, { type: 'chat' });\n      window.toastr.success('');\n    } catch (error) {\n      console.error(':', error);\n      window.toastr.error('');\n    }\n  }\n};\n\n// \nconst createSummaryWorldbook = async () => {\n  try {\n    // \n    let 