import { useSettingsStore } from './settings';

/**
 * 
 */
export async function fetchAvailableModels(): Promise<string[]> {
  const settings = useSettingsStore().settings;

  //  API endpoint
  if (!settings.api_endpoint || settings.api_endpoint.trim() === '') {
    throw new Error('API ');
  }

  const apiUrl = settings.api_endpoint.trim();
  console.log('URL:', apiUrl);

  try {
    new URL(apiUrl);
  } catch (e) {
    if (apiUrl.startsWith('/')) {
      throw new Error(
        `API  URL https://api.openai.com/v1/chat/completions${apiUrl}`,
      );
    }
    throw new Error(`API : ${apiUrl}`);
  }

  //  models API 
  const possibleEndpoints = [
    apiUrl.replace('/chat/completions', '/models'),
    apiUrl.replace('/v1/chat/completions', '/v1/models'),
    apiUrl + '/../models', // 
  ];

  console.log(':', possibleEndpoints);

  for (const modelsUrl of possibleEndpoints) {
    try {
      console.log(`: ${modelsUrl}`);

      const response = await fetch(modelsUrl, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${settings.api_key}`,
        },
      });

      console.log('Response status:', response.status);

      if (!response.ok) {
        console.log(` ${modelsUrl}  ${response.status}...`);
        continue;
      }

      const contentType = response.headers.get('content-type');
      if (!contentType || !contentType.includes('application/json')) {
        console.log(` ${modelsUrl}  JSON ...`);
        continue;
      }

      const data = await response.json();
      console.log('API :', data);

      // OpenAI  API 
      if (data.data && Array.isArray(data.data)) {
        const models = data.data.map((model: any) => model.id || model.name).filter(Boolean);
        console.log(':', models);
        return models;
      }

      //  API 
      if (Array.isArray(data)) {
        const models = data.map((model: any) => model.id || model.name).filter(Boolean);
        console.log(':', models);
        return models;
      }

      console.log('...');
    } catch (e) {
      console.log(` ${modelsUrl} :`, e);
      continue;
    }
  }

  throw new Error(' API  API ');
}

/**
 * 
 * @param start_id 
 * @param end_id 
 * @returns 
 */
export async function summarizeMessages(start_id: number, end_id: number): Promise<string> {
  const settings = useSettingsStore().settings;

  //  API endpoint
  if (!settings.api_endpoint || settings.api_endpoint.trim() === '') {
    throw new Error('API ');
  }

  //  URL
  const apiUrl = settings.api_endpoint.trim();
  console.log('API URL:', apiUrl);

  try {
    new URL(apiUrl);
  } catch (e) {
    //  URL
    if (apiUrl.startsWith('/')) {
      throw new Error(
        `API  URL https://api.openai.com/v1/chat/completions${apiUrl}`,
      );
    }
    throw new Error(`API : ${apiUrl}`);
  }

  // 
  // 
  const messages: Array<{ role: string; message: string }> = [];
  for (let i = start_id; i <= end_id; i++) {
    const msg = getChatMessages(i);
    if (msg.length > 0) {
      messages.push(...msg);
    }
  }

  if (messages.length === 0) {
    throw new Error('');
  }

  //  - 
  const summaryPrompt = `****


- 
- 
- 
- 
- 
- XML
- 
-  ${settings.max_tokens / 4} 
- ****




: []
: []

 1: 
 2: 
 3: 
...


${messages.map(msg => `[${msg.role}]: ${msg.message}`).join('\n\n')}

`;

  console.log(' APIURL:', apiUrl);
  console.log(':', {
    model: settings.model,
    messages: [{ role: 'user', content: summaryPrompt }],
    max_tokens: settings.max_tokens,
    temperature: settings.temperature,
  });

  //  OpenAI  API
  let response;
  try {
    response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${settings.api_key}`,
      },
      body: JSON.stringify({
        model: settings.model,
        messages: [
          {
            role: 'user',
            content: summaryPrompt,
          },
        ],
        max_tokens: settings.max_tokens,
        temperature: settings.temperature,
      }),
    });
  } catch (e) {
    console.error('fetch :', e);
    throw new Error(` API: ${(e as Error).message}`);
  }

  if (!response.ok) {
    let errorMessage = `API : ${response.status}`;
    try {
      const error = await response.json();
      errorMessage = error.error?.message || error.message || errorMessage;
    } catch (e) {
      //  JSON
      errorMessage = `API : ${response.status}`;
    }
    throw new Error(errorMessage);
  }

  const data = await response.json();
  if (!data.choices || !data.choices[0] || !data.choices[0].message?.content) {
    throw new Error('API ');
  }
  return data.choices[0].message.content;
}
