$(() => {
  // 
  setTimeout(() => {
    let isAdding = false;

    const addNavButton = () => {
      // 
      if (isAdding) {
        return;
      }
      isAdding = true;
      try {
        // 
        const possibleSelectors = [
          '#top-bar', //  HTML 
          '#top-settings-holder', //  HTML 
          '#topBar',
          '.topBar',
          '#navbar',
          '.navbar',
          'header',
          '.header',
          '#header',
          '.top_header',
          '.navigation',
        ];

        let topBar: JQuery<HTMLElement> = $();
        let foundSelector = '';

        for (const selector of possibleSelectors) {
          const found = $(selector);
          if (found.length > 0) {
            topBar = found;
            foundSelector = selector;
            console.log(`: ${selector}`);
            break;
          }
        }

        if (topBar.length === 0) {
          console.log('500ms...');
          setTimeout(addNavButton, 500);
          return;
        }

        console.log(`: ${foundSelector}`);

        // 
        const existingButton = $('#memoryNavButton');
        if (existingButton.length > 0) {
          console.log('mzrodyu');
          existingButton.remove();
        }

        //  -  top-bar top-settings-holder
        let targetContainer = topBar;
        if (foundSelector === '#top-bar') {
          const settingsHolder = $('#top-settings-holder');
          if (settingsHolder.length > 0) {
            targetContainer = settingsHolder;
            console.log(' top-settings-holder ');
          }
        }

        //  - 
        const navButton = $(
          `<div id="memoryNavButton" class="interactable" style="
            padding: 8px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            color: inherit;
            border-radius: 4px;
            transition: background 0.2s;
            position: relative;
          " title="mzrodyu">
            <span style="font-size: 24px"></span>
          </div>`,
        );

        // 
        console.log(':', targetContainer[0]?.id || '');
        console.log('HTML:', targetContainer[0]?.outerHTML?.substring(0, 300));

        //  targetContainer  top-settings-holder
        if (targetContainer[0]?.id === 'top-settings-holder') {
          console.log(' top-settings-holder ');
          const firstChild = targetContainer.children().first();
          if (firstChild.length > 0) {
            navButton.insertBefore(firstChild);
          } else {
            targetContainer.prepend(navButton);
          }
        } else {
          // 
          console.log('');
          targetContainer.prepend(navButton);
        }

        //  - 
        navButton.on('click', function (e) {
          e.preventDefault();
          e.stopPropagation();

          console.log('mzrodyu');

          // 
          const panel = $('#memoryManagementPanel');
          if (panel.length > 0) {
            if (panel.is(':visible')) {
              panel.fadeOut(200);
            } else {
              panel.fadeIn(200);
            }
          } else {
            toastr.error('');
          }
        });

        // 
        navButton
          .on('mouseenter', function () {
            $(this).css('background', 'rgba(255, 255, 255, 0.1)');
          })
          .on('mouseleave', function () {
            $(this).css('background', 'transparent');
          });

        console.log('mzrodyu:', targetContainer[0]?.id || '');
        console.log(':', navButton[0]?.parentElement?.children?.[0]?.className || '');
        console.log(':', navButton[0]);
        console.log(':', $('#memoryManagementPanel').length);
        isAdding = false;
      } catch (error) {
        console.error(':', error);
        isAdding = false;
        setTimeout(addNavButton, 500);
      }
    };

    // 
    addNavButton();

    // 2
    setInterval(() => {
      const button = $('#memoryNavButton');
      if (button.length === 0 && !isAdding) {
        console.log('...');
        addNavButton();
      }
    }, 2000);

    // UI
    if (typeof eventOn === 'function') {
      try {
        // 
        eventOn(tavern_events.CHAT_CHANGED, () => {
          console.log('...');
          setTimeout(() => {
            const button = $('#memoryNavButton');
            if (button.length === 0 && !isAdding) {
              console.log('...');
              addNavButton();
            }
          }, 500);
        });
      } catch (e) {
        console.log(':', e);
      }
    }
  }, 300);
});
